import Html from "../parts/Html";
export default class DetailDataArea extends Html {
  constructor(data={}) {
    super();
    
    var useKeysWithName = [
      { name: "", key: "kakaku" },
      { name: "", key: "koushinryo" },
      { name: "", key: "sonota_hiyo" },
      { name: "", key: "hokenryo" },
      { name: "", key: "keiyakukikan" },
      { name: "", key: "hoshokin" },
      { name: "", key: "shokyaku_sikihiki" },
      { name: "", key: "balcony" },
      { name: "", key: "bukken_shubetu" },
      { name: "()", key: "tikunensu" },
      { name: "", key: "houi" },
      { name: "", key: "kozo" },
      { name: "/", key: "shozoikai" },
      { name: "", key: "sotosu" },
      { name: "", key: "madori_utiwake" },
      { name: "", key: "balcony_menseki" },
      { name: "", key: "chushajo" },
      { name: "", key: "hikiwatashi" },
      { name: "()", key: "genjo" },
      { name: "", key: "kanri" },
      { name: "", key: "bikou" },
      { name: "", key: "shuhenshisetu" },
      { name: "", key: "torihikitaiyo" },
    ];
    
    // 
    var $detailDataArea = $(`
      <div class="detail-data-area">
        <h3></h3>
      </div>
    `);
    var $table = $(`<table class="detail-table"></table>`);
    $detailDataArea.append($table);
    
    var TRs = useKeysWithName.map( keyWithName => {
      var name = keyWithName.name;
      var key = keyWithName.key;
      var val = data[key];
      if ( key == "bikou" ) {
        val = val.replace(/\n/g, "<br>");
      }
      return $(`<tr>
        <th>${name}</th><td>${val}</td>
      </tr>`);
    });
    $table.append(TRs);
    
    this.$html = $detailDataArea;
  }
}


